import React, { useEffect, useState } from 'react'
// import logo from './logo.svg';
import Home from './Home/Home'
import Demo from './Demo/Demo'
import './App.scss'
import ThreeTest from './ThreeTest/ThreeTest'
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom'
import { useLocation } from 'react-router'
import Gallery from './Gallery/Gallery'
import Game from './Game/Game'
import Grant from './Grant/Grant'
import { RootState, RootDispatch } from './model'
import { useRematch } from './utils/useRematch'
import Logo from './assets/images/logo.png'

const mapState = (state: RootState) => ({
  ...state.main
})

const mapDispatch = (dispatch: RootDispatch) => ({
  ...dispatch.main
})

const infos = {
  cn: {
    navs: [
      '',
      '',
      '',
      '',
    ],
    walletText: ''
  },
  en: {
    navs: [
      'MAIN',
      'ALL Heroes',
      'GAME',
      'GRANT',
    ],
    walletText: 'Connect Wallet'
  }
}

const App = () => {
  // let history = useHistory();
  let location = useLocation()
  const [inChinese, setInChinese] = useState(!(location.pathname.indexOf('/en') > -1))
  const [burgerActive, setBurgerActive] = useState(false)

  const [{ userInfo }, { setFields, init }] = useRematch(mapState, mapDispatch)
  const [info, setInfo] = useState(infos[inChinese ? 'cn' : 'en'])

  useEffect(() => {
    setInChinese(!(location.pathname.indexOf('/en') > -1))
  }, [location])

  useEffect(() => {
    setInfo(infos[inChinese ? 'cn' : 'en'])
  }, [inChinese])

  useEffect(() => {
    init()
  }, [])

  const onClickBurger = () => {
    setBurgerActive(!burgerActive)
  }

  return (
    <div className="App">
      <Router>
        <nav className="navbar is-black" role="navigation" aria-label="main navigation">
          <div className="navbar-brand logoArea">
            {/* <a className="navbar-item"> */}
              {/* <span>KUNGFU HERO</span> */}
              <img src={Logo} className="logo" alt="" />
            {/* </a> */}

            <a
              role="button"
              className={burgerActive ? 'navbar-burger is-active' : 'navbar-burger'}
              aria-label="menu"
              aria-expanded="false"
              data-target="navbarBasicExample"
              onClick={onClickBurger}
            >
              <span aria-hidden="true"></span>
              <span aria-hidden="true"></span>
              <span aria-hidden="true"></span>
            </a>
          </div>

          <div
            id="navbarBasicExample"
            className={burgerActive ? 'navbar-menu is-active is-black' : 'navbar-menu is-black'}
          >
            <div className="navbar-start">
              <Link className="navbar-item" to={inChinese ? '/' : '/en'}>
                {info.navs[0]}
              </Link>

              <Link className="navbar-item" to={inChinese ? '/gallery' : '/en/gallery'}>
                {info.navs[1]}
              </Link>

              {/* <Link className="navbar-item" to={inChinese ? '/about' : '/en/about'}>
                
              </Link> */}

              <Link className="navbar-item" to={inChinese ? '/game' : '/en/game'}>
                {info.navs[2]}
              </Link>

              <Link className="navbar-item" to={inChinese ? '/grant' : '/en/grant'}>
                {info.navs[3]}
              </Link>

              {/* <Link className="navbar-item" to={inChinese ? '/demo' : '/en/demo'}>
                Demo
              </Link> */}

              {/* <Link className="navbar-item" to={inChinese ? '/3dModelTest' : '/en/3dModelTest'}>
                3dModelTest
              </Link> */}
            </div>
            <div className="navbar-end">
              <Link className="navbar-item" to={inChinese ? '/en' : '/'} onClick={setInChinese.bind(this, !inChinese)}>
                {inChinese ? 'En' : ''}
              </Link>
              <div className="navbar-item linkWaletContainer">
                <div className="button linkWallet">{info.walletText}</div>
              </div>
            </div>
          </div>
        </nav>
        <Switch>
          <Route exact path={['/', '/en', '/china-punk-homepage', '/kungfu-hero-homepage']}>
            <Home />
          </Route>
          <Route path={['/gallery', '/en/gallery']}>
            <Gallery />
          </Route>
          <Route path={['/game', '/en/game']}>
            <Game />
          </Route>
          <Route path={['/grant', '/en/grant']}>
            <Grant />
          </Route>
          <Route path={['/demo', '/en/demo']}>
            <Demo />
          </Route>
          <Route path={['/3dModelTest', '/en/3dModelTest']}>
            <ThreeTest />
          </Route>
        </Switch>
      </Router>
    </div>
  )
}

export default App
