import { createModel } from '@rematch/core'
import { ethers } from 'ethers'
// import { BigNumber } from 'bignumber.js'
// import { KongFu__factory } from '../contract/types/ethers-contracts/factories/KongFu__factory'
import { KongFu__factory } from '../contract/types/ethers-contracts/factories/KongFu__factory'
import { RootModel } from '.'

declare global {
  interface Window {
    ethereum: ethers.providers.ExternalProvider
  }
}

interface UserInfo {
  address: string
  balance?: string
  unlockableAmount?: string
  pendingUnlockAmount?: string
}

interface MainState {
  userInfo: UserInfo
  ethersProvider?: ethers.providers.Web3Provider
  signer?: ethers.providers.JsonRpcSigner
}

const initMainState: MainState = {
  userInfo: {
    address: ''
  }
}

export const main = createModel<RootModel>()({
  state: initMainState,
  reducers: {
    setFields(state, fields: Partial<MainState>): MainState {
      return {
        ...state,
        ...fields
      }
    },
    setUserInfo(state, fields: Partial<UserInfo>): MainState {
      let newUserInfo = {
        ...state.userInfo,
        ...fields
      }
      return {
        ...state,
        userInfo: newUserInfo
      }
    }
  },
  effects: (dispatch) => ({
    async init() {
      const ethereum = window.ethereum
      if (ethereum) {
        let ethersProvider = new ethers.providers.Web3Provider(ethereum, 'any')
        console.log(' ~ file: main.ts ~ line 57 ~ init ~ ethersProvider', ethersProvider)
        let signer = ethersProvider.getSigner()
        let walletAccounts
        console.log(' ~ file: main.ts ~ line 58 ~ init ~ signer', signer)
        try {
          walletAccounts = await ethereum.request!({
            method: 'eth_requestAccounts'
          })
          console.log(' ~ file: main.ts ~ line 62 ~ init ~ walletAccounts', walletAccounts)
        } catch (error) {
          console.log(' ~ file: main.ts ~ line 66 ~ init ~ error', error)
          if (error?.code === 4001) {
            // 
          }
        }
        // console.log(' ~ file: main.ts ~ line 100 ~ init ~ walletAccounts', walletAccounts)
        // console.log(' ~ file: main.ts ~ line 97 ~ init ~ walletAccounts', walletAccounts)
        dispatch.main.setUserInfo({ address: walletAccounts[0] })
        dispatch.main.setFields({
          ethersProvider,
          signer
        })
        dispatch.main.initContract()
      }
    },
    async initContract(_payload: any, state) {
      let { ethersProvider, signer } = state.main
      console.log(' ~ file: main.ts ~ line 107 ~ initContract ~ ethersProvider, signer', ethersProvider, signer)
      if (ethersProvider && signer) {
        // 
        let KongFuContract = KongFu__factory.connect('0xf8bD0D26E6DCf2A120331E15Ae01AD1CF1678b6f', signer)
        console.log(' ~ file: main.ts ~ line 87 ~ initContract ~ KongFuContract', KongFuContract)
        let result = await KongFuContract.mint(1, { value: ethers.utils.parseEther('0.05') })
        console.log(' ~ file: main.ts ~ line 91 ~ initContract ~ result', result)
      }
    }
  })
})
