import React from 'react';
import * as THREE from "three";
import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader'
// import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader'
let fbxUrl = require('./dance.fbx');
export default class ThreeTest extends React.Component {
  myRef: React.RefObject<HTMLDivElement>;
  constructor(props: any) {
    super(props);
    this.state = { seconds: 0 };
    this.myRef = React.createRef();
  }

  componentDidMount() {
    if (!this.myRef.current) {
      return
    }
    var scene = new THREE.Scene();
    // scene.add(new THREE.AmbientLight(0xcccccc));
    var camera = new THREE.PerspectiveCamera( 75, window.innerWidth/window.innerHeight, 0.1, 1000 );
    var renderer = new THREE.WebGLRenderer();
    renderer.setSize( window.innerWidth, window.innerHeight );
    this.myRef.current.appendChild( renderer.domElement );
    // var geometry = new THREE.BoxGeometry( 1, 1, 1 );
    // var material = new THREE.MeshBasicMaterial( { color: 0x00ff00 } );
    // var cube = new THREE.Mesh( geometry, material );
    // scene.add( cube );
    // camera.position.z = 5;
    // var animate = function () {
    //   requestAnimationFrame( animate );
    //   cube.rotation.x += 0.01;
    //   cube.rotation.y += 0.01;
    //   renderer.render( scene, camera );
    // };
    // animate();
    // camera                                                                   
    // const camera = new THREE.PerspectiveCamera(30, 800 / 600, 1, 10000);
    camera.position.set(0, 100, 500);
    // camera.up.set(0, 1, 0);
    // camera.lookAt(new THREE.Vector3(0, 0, 0));

    // scene and lights                                                         
    // const scene = new THREE.Scene();

    // load fbx model and texture                                               
    const objs: any[] = [];
    // console.log(typeof THREE.FBXLoader)
    const loader = new FBXLoader();
    loader.load(fbxUrl.default, model => {
        // model is a THREE.Group (THREE.Object3D)                              
        const mixer = new THREE.AnimationMixer(model);
        // animations is a list of THREE.AnimationClip                          
        mixer.clipAction(model.animations[0]).play();
        scene.add(model);
        objs.push({model, mixer});
    });

    // animation rendering                                                      
    const clock = new THREE.Clock();
    (function animate() {
        // animation with THREE.AnimationMixer.update(timedelta)                
        objs.forEach(({mixer}) => {mixer.update(clock.getDelta());});
        renderer.render(scene, camera);
        requestAnimationFrame(animate);
    })();

    // const loader = new OBJLoader();
    // loader.load('../static/model/dance.obj', (root) => {
    //   scene.add(root)
    //   renderer.render(scene, camera);
    // })
  }

  componentWillUnmount() {
    // clearInterval(this.interval);
  }

  render() {
    return (
      <div id="canvas-container" ref={this.myRef}>
      </div>
    );
  }
}