const Section5 = ({info}) => {
  const history = useHistory()
  const location = useLocation();
  const navToGamePage = () => {
    if (location.pathname.indexOf('/en') > -1) {
      history.push('/game')
    } else {
      history.push('/en/game')
    }
  }
  return (
    <section className="hero is-black section5">
      <div className="container">
        <div className="titleArea">
          <div className="titleText">{info.title}</div>
        </div>
        <div className="columns">
          <div className="column is-7">
            <div className="relative">
              <div className="imageTV" onClick={navToGamePage} style={{backgroundImage: `url(${info.imageTV})`}}></div>
            </div>
          </div>
          <div className="column is-5 relative">
            <div className="content">{info.content}</div>
            {info.items.map((item) => (
              <div className="item">{item}</div>
            ))}
            <div className="imageTVMan" style={{backgroundImage: `url(${info.imageTVMan})`}}></div>
          </div>
        </div>
        <div className="buttonContainer">
          <div className="button" onClick={navToGamePage}>{info.buttonText}</div>
        </div>
      </div>
    </section>
  )
}

