const Gallery = () => {
  let location = useLocation();
  const [inChinese, setInChinese] = useState(!(location.pathname.indexOf('/en') > -1))
  const [info, setInfo] = useState(infos[inChinese ? 'cn' : 'cn'])

  useEffect(() => {
    const isChinese = !(location.pathname.indexOf('/en') > -1)
    setInChinese(isChinese)
    setInfo(infos[inChinese ? 'cn' : 'cn'])
  }, [location]);


  return (
    <div className="gallery">
      <div className="hero is-black header">
        {info.title}
      </div>
      <div className="searchContainer container">
        <div className="control has-icons-left has-icons-right is-medium">
          <input className="input is-medium" type="text" placeholder="ID" />
          <span className="icon is-small is-left">
            <i className="fas fa-search"></i>
          </span>
          <span className="icon is-small is-right">
            <i className="fas fa-times-circle"></i>
          </span>
        </div>
      </div>
      <div className="container bottom-main">
        <div className="columns">
          <div className="column is-2 category-main">
            {
              info.categories.map((category) => (
                <div className="level-0">
                  <div className="titleArea">
                    <div className="categoryName">{category.name}</div>
                    <div className="icon" style={{backgroundImage: `url(${upIcon})`}}></div>
                  </div>
                  {
                    category.children && category.children.length > 0 ? (
                      <div className="childrenArea">
                        {category.children.map((child) => (
                          <div className="childTitle">{child.name}</div>
                        ))}
                      </div>
                    ) : null
                  }
                </div>
              ))
            }
          </div>
          <div className="column is-offset-1 is-9 gallery-main">
            <div className="columns is-mobile is-multiline">
              {
                info.images.map((image) => (
                  <div className="column is-half-mobile is-one-third-desktop">
                    <div className="image" style={{backgroundImage: `url(${image.url})`}}></div>
                    <div className="name">{image.name}</div>
                  </div>
                ))
              }
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Gallery