import { imgConfig } from '../const/imgConfig'
import './Demo.css'
import { useEffect, useState } from 'react'
const requireContext = require.context('../assets/611update', true, /^\.\/.*\.png$/)

const baseUrl = '/china-punk-homepage/static/media'

const getUrl = (model: string, id: string) => {
  // if (Number(id) < 10) {
  //   id = `0${id}`
  // }
  return `${baseUrl}/${model}${id}.png`
}

function Demo() {
  const [punk, setPunk] = useState({})

  useEffect(() => {
    let obj = {}
    Object.keys(imgConfig).forEach((key) => {
      obj[key] = imgConfig[key][0]
    })
    setPunk(obj)
  }, [])

  const genRandom = () => {
    let newRandom = {}
    Object.keys(imgConfig).forEach((key: string) => {
      newRandom[key] = imgConfig[key][Math.ceil(Math.random() * imgConfig[key].length) - 1]
    })
    console.log(' ~ file: Demo.tsx ~ line 36 ~ genRandom ~ newRandom', newRandom)
    setPunk(newRandom)
  }

  return (
    <div className="App" style={{ width: '100vw' }}>
      <div className="demo">
        {Object.keys(punk).map((key) => {
          return <img src={getUrl(key, String(punk[key]))} alt="key" key={key} />
        })}
      </div>
      <div style={{ position: 'fixed', right: 0 }}>
        <button
          className="random-button"
          onClick={() => {
            genRandom()
          }}
        >
          Random
        </button>
        {Object.keys(punk).map((key) => {
          return (
            <div style={{ marginTop: 10 }}>
              {key}
              <input
                type="number"
                style={{ marginLeft: 10, width: 60 }}
                value={punk[key]}
                max={imgConfig[key].length}
                min={1}
                onChange={(e) => {
                  console.log(e.target.value)
                  setPunk({
                    ...punk,
                    [key]: e.target.value
                  })
                }}
              />
            </div>
          )
        })}
      </div>
    </div>
  )
}

export default Demo
